import React,{useState, useEffect} from 'react';
import axios from 'axios';
import { Card, Grid, Typography } from '@material-ui/core';
import useStyles from './useStyles';
import { Card as CardLibrary, CardWrapper } from 'react-swipeable-cards';
import CardQuote from './Card';
import Test from './Test'

const Quotes = () => {
    const [quote, setQuotes] = useState([])
    const { root, cardStyles } = useStyles();
    console.log(quote)
    const pageNumber = Math.random()*9
    useEffect(async () => {
        const response = await axios.get(`https://api.quotable.io/quotes?page=${pageNumber}`)
        setQuotes(response.data.results)
    }, [])

    return (
        <div className={root}>
            {/* {<CardQuote quotes={quote}/> ? initCard() : initCard() } */}
        {quote && <Test quotes={quote} />}
        </div>
    )
}


export default Quotes;